import {useForm} from "react-hook-form";
import {Button, TextField} from "@mui/material";

import p from "../../Profile/Posts/Posts.module.css";


import React, {useState} from "react";
import AddIcon from '@mui/icons-material/Add';
import AddAPhotoIcon from '@mui/icons-material/AddAPhoto';
import GifBoxRoundedIcon from '@mui/icons-material/GifBoxRounded';
import {useDispatch} from "react-redux";
import {useAppSelector} from "../../../state/redux-store";
import {sendMessage} from "../../../state/chat-reducer";


type LoginUserModelType = {
    post: string,

};

type AddPostFormType = {
    addPost?: (newPostBody: string) => void,
    buttonType: string
}


export const TextInputMessageFormChat = React.memo(({buttonType}: AddPostFormType) => {

    const {register, handleSubmit, resetField, formState: {errors}} = useForm<LoginUserModelType>();


    const onSubmit = handleSubmit(data => {
        if (!message) {
            return
        }
        dispatch(sendMessage(message))
        setMessage('')
    })


    ///block WebSocket


    const [message, setMessage] = useState('')
    const dispatch = useDispatch()

    const status = useAppSelector((state) => state.chat.status)


    // const sendMessageHandler = () => {
    //     if (!message) {
    //         return
    //     }
    //     dispatch(sendMessage(message))
    //     setMessage('')
    // }


    return <>
        <form onSubmit={onSubmit}>
            <div className={p.flex_container}>


                <div className={p.text_field}>
                    <TextField
                        {...register("post", {required: true, maxLength: 300})}
                        error={!!errors.post}
                        type='text'
                        onChange={(e) => setMessage(e.currentTarget.value)}
                        value={message}
                        id="filled-multiline-flexible"
                        label="Add your post"

                        multiline
                        color={"info"}
                        style={{width: "100%", padding: "5px" , height:'75px'}}

                        sx={{
                            bgcolor: 'AppWorkspace',
                            boxShadow: 1,
                            borderRadius: 2
                        }}

                    />
                </div>

                <div className={p.btn_container}>

                    <Button className={p.btn}
                            style={{backgroundColor: '#6c7272'}}
                            size='small'
                            type="submit"
                            variant="contained"
                            disabled={status !== 'ready'}
                            onClick={onSubmit}

                    >{buttonType}</Button>


                    <div className={p.image_upload}>


                        <label htmlFor="contained-button-file">
                            <AddIcon/>
                        </label>


                        <AddAPhotoIcon/>
                        <GifBoxRoundedIcon/>
                    </div>

                </div>


            </div>
        </form>

    </>
});

