import React, {useEffect} from "react";

import h from '../Dialogs.module.css'

import {TextInputMessageFormChat} from "./TextInputMessageFormChat";
import {useDispatch} from "react-redux";
import {useAppSelector} from "../../../state/redux-store";
import {startMessagesListening, stopMessagesListening} from "../../../state/chat-reducer";
import {Message} from "./Message";
import ReactScrollableFeed from 'react-scrollable-feed'
import {CircularProgress} from "@mui/material";
import {useNavigate} from "react-router";



function Dialogs() {

    const dispatch = useDispatch()
    let navigate = useNavigate();
    const isAuth=useAppSelector(state => state.auth.isAuth)
    const status = useAppSelector(state => state.chat.status)

    useEffect(() => {
        dispatch(startMessagesListening())
        return () => {
            dispatch(stopMessagesListening())

        }
    }, [])


    useEffect(() => {
        if (!isAuth) {
            return navigate("/login");
        }

    }, [isAuth]);


    // if(! ){
    //     return <span style={{textAlign:'center'}}><CircularProgress /></span>
    // }

    return (
        <>
            {status === 'error' && <div>Some error occured. Please refresh the page</div>}
            <div className={h.main}>
                <div style={{textAlign: 'center'}}>
                    <h4 style={{color: '#525252'}}>Messages</h4>
                </div>


                <Message/>

                <div className={h.addMessageBorder}>
                    <TextInputMessageFormChat buttonType={'addMessage'}/>
                </div>

            </div>
        </>
    )
}

export default Dialogs


